@import "../common/styles/sass/variables";

.skillsBlock {
  min-height: 100vh;
  background-color: $bgColor;

  .skillsContainer {

    /*align-items: center;*/

    flex-direction: column;
    justify-content: flex-start;

    .skills {
      border: 1px solid;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    @media screen and (max-width: 624px) {
      justify-content: center;
      align-items: center;
      flex-direction: row;
    }
  }
}

